<template>
    <div class="upload-view">
      <h2>Upload de Documentos</h2>
      <p>Faça o upload de até 100 documentos PDF para avaliação.</p>
      
      <div class="upload-area">
        <input 
          type="file" 
          ref="fileInput" 
          multiple 
          accept="application/pdf,.pdf,.doc,.docx" 
          @change="handleFileUpload" 
          class="file-input"
        />
        
        <div class="upload-box" @click="triggerFileInput">
          <div v-if="selectedFiles.length === 0">
            <i class="upload-icon">📄</i>
            <p>Clique aqui ou arraste os arquivos para esta área</p>
            <small>Limite de 100 documentos</small>
          </div>
          <div v-else>
            <p>{{ selectedFiles.length }} arquivo(s) selecionado(s)</p>
            <ul class="file-list">
              <li v-for="(file, index) in selectedFiles" :key="index">
                {{ file.name }}
              </li>
            </ul>
          </div>
        </div>
      </div>
      
      <div class="actions">
        <button 
          @click="submitFiles" 
          :disabled="selectedFiles.length === 0"
          :class="{ disabled: selectedFiles.length === 0 }"
        >
          Prosseguir
        </button>
        <p v-if="error" class="error-message">{{ error }}</p>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'UploadView',
    
    data() {
      return {
        selectedFiles: [],
        error: null
      }
    },
    
    methods: {
      triggerFileInput() {
        this.$refs.fileInput.click();
      },
      
      handleFileUpload(event) {
        const files = Array.from(event.target.files);
        
        // Verificar se ultrapassa o limite
        if (files.length > 100) {
          this.error = 'O limite é de 100 documentos. Por favor, selecione menos arquivos.';
          return;
        }
        
        this.selectedFiles = files;
        this.error = null;
      },
      
      submitFiles() {
        if (this.selectedFiles.length === 0) {
          return;
        }
        
        // Adicionar arquivos ao store
        this.$store.dispatch('addDocuments', this.selectedFiles);
        
        // Redirecionar para a próxima etapa
        this.$router.push('/description');
      }
    }
  }
  </script>
  
  <style scoped>
  .upload-view {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .upload-area {
    width: 100%;
    margin: 20px 0;
  }
  
  .file-input {
    display: none;
  }
  
  .upload-box {
    border: 2px dashed #ccc;
    border-radius: 8px;
    padding: 40px;
    text-align: center;
    cursor: pointer;
    transition: border-color 0.3s ease;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .upload-box:hover {
    border-color: #4CAF50;
  }
  
  .upload-icon {
    font-size: 48px;
    margin-bottom: 10px;
  }
  
  .file-list {
    max-height: 200px;
    overflow-y: auto;
    text-align: left;
    padding-left: 20px;
  }
  
  .actions {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .error-message {
    color: red;
    margin-top: 10px;
  }
  </style>